import { Img } from 'assets/img';
import { useWindowSize } from 'hooks';
import { Trans } from 'react-i18next';
import { isLargerMobile, isLargerTablet } from 'utils/helper';

function Hero() {
  const [width] = useWindowSize();

  const renderBg = () => {
    if (isLargerTablet(width))
      return <img className="cover-img" src={Img.heroUrl} alt="hero" />;
    if (isLargerMobile(width))
      return <img className="cover-img" src={Img.heroTabletUrl} alt="hero" />;
    return <img className="cover-img" src={Img.heroMobileUrl} alt="hero" />;
  };

  return (
    <div className="hero">
      {renderBg()}
      <div className="hero__filter1"></div>
      <div className="hero__filter2"></div>
      <div className="hero__filter3"></div>
      <div className="hero__content container">
        <h1>
          <Trans i18nKey="hero.solution">
            Giải pháp <br />
            Mua Trước - Trả Sau <br /> Phổ Biến Nhất Việt Nam
          </Trans>
        </h1>
      </div>
    </div>
  );
}

export default Hero;
